.top{color:white;
background:url('https://upload.wikimedia.org/wikipedia/commons/1/1a/Cole_Thomas_The_Consummation_The_Course_of_the_Empire_1836.jpg');
margin:0px;
border:1px solid rgba(255, 99, 71, 0);
background-repeat: no-repeat;
background-size: auto;
background-position: center;
background-size:  100%;
height: 30vh;
}
li{margin-top:100px;margin-left:50px;margin-right:200px;border:1px solid red}
#nav{
    
    background:rgb(83, 81, 81);
    height:40px;
    overflow: hidden;

    display:flex;
    align-items: center;
    justify-content: space-evenly;
    .link{text-decoration: none;color:white;padding:15px;font-size: 18px;font-weight: 300;
    font-family:sans-serif;
    
    }

    
    .link:hover{background: black;}
    div{
    margin-left: 100px;
    margin-right:100px;
    display:flex;
    align-items: center;
    justify-content: space-between;
    width:90%
    }
}
#router{
   display:flex;
   justify-content: space-between;
   margin-left: 40px;
}
.swiper{
    border:1px solid red
}
.left{padding-top: 100px;width:35%;background-color:rgb(241, 241, 241);}
@media (max-width:1000px) {
    #router{
        flex-direction: column;
        
    }
    .left{width:100%}
}
i{color:blue}